.CreateForm .container {
    margin-top: 5rem;
    width: 75%;
    margin: 10rem auto;
    padding: 2rem;
    background: #eeeff1c5;
    border-radius: 7px;
    box-shadow: 1px 3px 2px rgba(0, 0, 0, 0.404), -2px -2px 5px rgba(0, 0, 0, 0.397);
}

.CreateForm h1 {
    font-size: 3em;
    text-align: center;
    font-weight: 300;
    color: rgb(141, 14, 141);
    /* padding: 1rem; */
}

.CreateForm label {
    display: block;
    font-size: 1.8em;
    padding: 10px;
}

.CreateForm input, textarea {
    padding: 1rem;
    width: 95%;
    margin: 1rem;
    border: none;
    border-bottom: 1px solid rgb(141, 14, 141);
    transition: 200ms ease-in-out;
}

.CreateForm input:focus, textarea:focus {
    outline-color: blue;
    outline-width: 2px;
    outline: none;
    border-bottom: 2px solid rgb(141, 14, 141);
}

.CreateForm .btn.btn-default {
    cursor: pointer;
    color: #111;
    display: block;
    width: 38%;
    background:linear-gradient(to right, gold, rgb(233, 205, 50));
    border: none;
    margin: 0 auto;
}




